  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

:root {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
